* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', sans-serif;
}

.App {
  background-image: url(./Assets/cold.jpeg);
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
}

.App.warm {
  background-image: url(./Assets/warm.jpeg);
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, .65));
  padding: 25px;
}

.search-box {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 0 0 75px;
}

.search-box .search-bar {
  width: 60%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgb(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;

  transition: 0.4s ease;
}

.search-box .search-bar:focus, .search-box .search-bar:hover {
  background-color: rgba(255, 255, 255, 0.75);
}

.location-box .location {
  color: #FFFF;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 1.5px 1.5px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #FFFF;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  padding: 5px;
  text-align: center;
  text-shadow: 1.5px 1.5px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #FFFF;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weather-box, .weather {
  color: #FFFF;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

/* Mobile media query */
@media only screen and (max-width: 500px) {
  .search-box .search-bar {
    width: 75%; /* increase by 15 pp */
  }
}